.song-container {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    img { width: 20%; border-radius: 50%; }

    h2 { padding: 3rem 1rem 1rem; }

    h3 { font-size: 1rem; }
}

@media screen and (max-width: 768px){
    .song-container {
        img { width: 60%;  }
    }
}

.rotate {
    transition: all 0.5s ease;
    animation: rotation 59s infinite linear;
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(359deg);
    }
    
}